<template>
  <div class="app-container" id="brigadeAlarmStatistics">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm"  class="filter-container" :inline="true">
      <el-form-item prop="value1">
        <el-date-picker
          v-model="ruleForm.value1"
          type="date"
          placeholder="选择日期"
          value-format="timestamp"
          :picker-options="pickerOptions"
        ></el-date-picker>
      </el-form-item>
      <span>-</span>
      <el-form-item prop="value2">
        <el-date-picker
          v-model="ruleForm.value2"
          type="date"
          placeholder="选择日期"
          value-format="timestamp"
          :picker-options="pickerOptions"
        ></el-date-picker>
      </el-form-item>
      <el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button>
         <!-- <el-button type="primary" icon="el-icon-search" v-if="power==1" @click="excute">执行统计</el-button> -->
         <el-button type="primary"  @click="exportExcel" icon="iconfont icondaochu">导出</el-button>
    </el-form>
    <div class="tableBox">
      
    <el-table
      :key="tableKey"
      v-loading="listLoading"
      :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)"
      border
      fit
      highlight-current-row
      style="width: 100%;"
      :row-class-name="tableRowClassName"
    >
      <el-table-column
        fixed
        label="序号"
        :index="indexMethod"
        prop="id"
        sortable="custom"
        align="center"
        width="80"
        type="index"
      ></el-table-column>

      <el-table-column label="大队名称" width="150px" align="center" fixed>
        <template slot-scope="{row}">
          <span class="link-type">{{ row.teamName}}</span>
        </template>
      </el-table-column>

      <el-table-column label="枪支偏离车辆" width="120px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. gunLostCar}}</span>
        </template>
      </el-table-column>
      <el-table-column label="车辆偏移路线" width="120px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carLostRoad}}</span>
        </template>
      </el-table-column>
      <el-table-column label="拆卸报警" width="120px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. deviceBroken}}</span>
        </template>
      </el-table-column>
      <el-table-column label="枪支网络异常" width="150px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. gunGpsMissing}}</span>
        </template>
      </el-table-column>
      <el-table-column label="车辆信号丢失" width="120px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carGpsMissing}}</span>
        </template>
      </el-table-column>
      <el-table-column label="同一辆车周围少于2支枪" width="180px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. gunNotEnough}}</span>
        </template>
      </el-table-column>
      <el-table-column label="未到规定时间内打卡" width="180px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carCheckinTimeout}}</span>
        </template>
      </el-table-column>

      <el-table-column label="车辆道路变换" width="120px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carChangeRoad}}</span>
        </template>
      </el-table-column>
      <el-table-column label="电量不足" width="150px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. gunLowBattery}}</span>
        </template>
      </el-table-column>
      <el-table-column label="自定义超速" width="120px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carOverSpeed}}</span>
        </template>
      </el-table-column>
      <el-table-column label="一键报警" width="150px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carOneKeyAlarm}}</span>
        </template>
      </el-table-column>
      <el-table-column label="车载定位系统推送的超速" width="180px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carOverSpeedCorrect}}</span>
        </template>
      </el-table-column>
      <el-table-column label="未保留笼统的异常" min-width="180px" align="center">
        <template slot-scope="{row}">
          <span>{{ row. carNotDefined}}</span>
        </template>
      </el-table-column>
      <el-table-column label="总计" min-width="120px" align="center" fixed="right">
        <template slot-scope="{row}">
          <span>{{ row. amount}}</span>
        </template>
      </el-table-column>
    </el-table>
    </div>


    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pagesize"
      :total="list.length"
      @current-change="handleCurrentChange"
       v-show="list.length>0"
      @size-change="handleSizeChange"
    ></el-pagination>
  </div>
</template>


<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { logsByTeam } from "../../../../api/alarmStatistics.js";//异常统计列表
import { logsInvoke,exportExcelReq } from "../../../../api/managementArchives/common";//执行统计（在数据没有被查询过的情况下可能查询不到，需要执行统计一段时间后再查询就会有结果了）
export default {
  name: "firearmsAlarmStatistics",
  components: { Pagination },
  data() {
    return {
      tableKey: 0,
      pagesize: 10,//每页条数
      currpage: 1,//页码
      indexMethod: 1, // 序号
      list: [],//异常统计列表数据
      total: 0,//异常统计列表数据总条数
      listLoading: false,
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now(); // 今天也不能选，因为今天的数据还未统计完全
        }
      },
      ruleForm: {
        value1: "", // 开始或者结束时间
        value2: ""
      },//查询时间
      rules: {
        value1: [
          {
            type: "date",
            required: true,
            message: "请选择起止日期",
            trigger: "change"
          }
        ],
        value2: [
          {
            type: "date",
            required: true,
            message: "请选择起止日期",
            trigger: "change"
          }
        ]
      }
    };
  },
  created() {},
  beforeDestroy() {},
  methods: {
    //异常统计列表请求
    getList(item) {
      if (item.length > 31) {
        this.$message.warning("请搜素31天内的数据,请重新选择!");
        return;
      }
      this.listLoading = true;
      this.indexMethod = 1;
      this.currpage = 1;
      logsByTeam({ dates: item }).then(response => {
        if (response.status == 0) {
          if (response.data !== null) {
            this.list = response.data;
            this.listLoading = false;
            this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
          } else {
            this.list = [];
            this.listLoading = false;
            this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
          }
        } else {
          this.$message.error(res.mes);
        }

        // Just to simulate the time of the request
      });
    },
    //执行统计
     excute() {
      let _t = this;
      // 查找数据
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          if (_t.dealDate().length > 1) {
            this.$message.warning("请选择同一天");
            return;
          }
          logsInvoke({ startTime:this.ruleForm.value1,endTime:this.ruleForm.value1+24*3600*1000 }).then(response => {
            if (response.status == 0) {
              this.$message.success(response.msg)
            } else {
               this.$message.error(response.msg)  
            }
          });
        }
      });
    },
     exportExcel() {
       let _t = this;
      // 查找数据
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          let item = this.dealDate();

      if (item.length > 31) {
        this.$message.warning("请搜素31天内的数据,请重新选择!");
        return;
      }
      this.$message.warning("稍等，正在请求数据，请勿重复点击哦");
      exportExcelReq({ dates: item,excelType:2,title:'大队报警统计' }).then(res => {
        try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
        let disposition = res.headers;
        let endDate = "";
        if (item.length > 1) {
          endDate = item[item.length - 1];
        }
        var fileName = item[0] + "----" + endDate + "大队报警统计" + ".xls";
        let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
            }
      });
        }
      });
     
    },
    dealDate() {
      // 处理时间数组
      let _t = this;
      function formatTime(time) {
        // 格式时间
        let d = new Date(time),
          y = d.getFullYear(),
          m = d.getMonth() + 1,
          day = d.getDate();
        m = m < 10 ? "0" + m : m;
        day = day < 10 ? "0" + day : day;
        return `${y}-${m}-${day}`;
      }
      let oneDay = 1000 * 60 * 60 * 24, // 一天的毫秒数
        s = Math.min(_t.ruleForm.value1, _t.ruleForm.value2), // 取小的值作为开始时间
        e = Math.max(_t.ruleForm.value1, _t.ruleForm.value2) + oneDay - 1, // 大的值作为结束时间并且加上一天的毫秒
        len = Math.ceil((e - s) / oneDay), // 用于将日期遍历
        dayArr = []; // 传到后台的日期数组
      for (let i = 0; i < len; i++) {
        let time = formatTime(s + oneDay * i);
        dayArr.push(time);
      }
      return dayArr;
    },
    //点击搜索
    search() {
      let _t = this;
      // 查找数据
      _t.$refs.ruleForm.validate(valid => {
        if (valid) {
          _t.getList(_t.dealDate());
        }
      });
    },
    //点击跳转页码
    handleCurrentChange(cpage) {
      this.currpage = cpage;
      this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
    },
    //改变每页显示条数
    handleSizeChange(psize) {
      this.pagesize = psize;
      this.indexMethod = (this.currpage - 1) * this.pagesize + 1;
    },
    handleSelectionChange(val) {},
     tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
  }
};
</script>
<style lang="scss" scoped>

#brigadeAlarmStatistics {
  .filter-container {
    .el-form-item {
      margin-bottom: 0;
      margin: 0 20px 0 20px;
    }
  }
  .el-pagination{
    margin-top:20px;
  }
}

</style>












